import {
	mapMutations
} from 'vuex';

export default {
	data() {
		return {
			phone: '',
			password: '',
			registering: false,
			sendText: '获取验证码',
			sendDisabled: false
		}
	},
	onLoad() {

	},
	methods: {
		...mapMutations(['login']),
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.navigateBack();
		},
		toLogin() {
			uni.redirectTo({
				url: "/pages/public/login"
			})
		},
		doGetVerify() {
			const that = this
			if (!that.phone || that.phone.length != 11) {
				uni.showToast({
					title:'请输入正确手机号！',
					icon:'none'
				})
				return
			}
			that.$api.request('user', 'sendVerifyCode', {
				phone: that.phone,
			}).then(res => {
				debugger
				that.sendDisabled = true
				let sec = 60
				let interval = setInterval(() => {
					sec--;
					that.sendText = sec + 's后重发'
					if (sec <= 0) {
						that.sendDisabled = false
						that.sendText = "获取验证码"
						clearInterval(interval)
					}
				}, 1000)
			})
			
		},
		async doRegister() {
			const that = this
			that.registering = true;
			that.$api.request('user','register', {
				phone: that.phone,
				password: that.password,
				verifyCode: that.verifyCode
			}, (failres => {
				uni.showToast({
					title:failres.errmsg
				})
				that.registering = false
			})).then(res => {
				//注册成功
				that.registering = false
				uni.redirectTo({
					url: './login'
				})
			})
		}
	},

}
